@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/all.min.css);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
/* Font Awesome Icon */

/* Google Fonts */

* {
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
  box-sizing: border-box;
}

body {
  margin: 0;
  line-height: 1.5;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
}

.bold {
  font-weight: bold;
}

.mb-4 {
  margin-bottom: 40px;
}

.my-3 {
  margin: 30px;
}

.h-center {
  position: absolute;
  top: 35%;
  left: 3%;
  font-size: 20px;
}

.form-control:focus {
  box-shadow: inset 0 -1px 0 #ddd;
}

.rounded {
  border-radius: 10px;
}

.centered {
  display: flex;
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
}

.container {
  width: 90%;
}

.searchbar {
  width: 45%;
  position: relative;
  border-radius: 50px;
  padding: 0 10px;
}

